.boxes {
	position: relative;
	color: $white;
	@include cover;
	&__inner {
		padding-top: 69px;
		padding-bottom: 92px;
		@include sm {
			padding-top: 50px;
			padding-bottom: 50px; } } }

.box {
	padding: 32px 50px 42px;
	border: 1px solid rgba($white, 0.5);
	text-align: center;
	@include lg {
		padding: 30px 20px 35px; }
	@include sm {
		padding: 30px 15px 35px; }
	&__title {
		font-family: $roboto_condensed;
		@include f(60px, 700, 79px);
		letter-spacing: -0.38px;
		@include lg {
			@include f(48px, null, 56px); }
		@include sm {
			@include f(36px, null, 46px); }
		&:after {
			content: '';
			display: block;
			margin: 18px auto 56px;
			border-radius: 2px;
			width: 66px;
			height: 2px;
			background: $radicalRed;
			@include lg {
				margin: 10px auto 30px; } } }
	&__content {
		margin-bottom: 40px;
		font-family: $roboto;
		@include f(16px, 300, 19px);
		letter-spacing: 0.49px;
		@include sm {
			margin: 0 auto 40px;
			max-width: 420px; } } }
