.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;
	height: 166px;
	&_border {
		border-bottom: 1px solid rgba(#D8D8D8, 0.25); }
	@include lg {
		height: 135px; }
	@include md {
		height: 76px; }
	&__inner {
		margin: 0 auto;
		padding: 0 32px;
		max-width: 1600px;
		@include md {
			padding: 0 15px; } }
	&__logo {
		float: left;
		margin: 20px 0 0 156px;
		@include lg {
			margin-left: 0; } }
	&__btn {
		float: right;
		margin: 15px -10px 0 0;
		display: none;
		@include md {
			display: block; } } }

.logo {
	vertical-align: top;
	display: inline-block;
	img {
		display: block;
		@include lg {
			height: 80px;
			width: auto; }
		@include md {
			height: 40px;
			width: auto; } } }

.nav {
	float: right;
	margin-top: 110px;
	font-weight: 400;
	@include lg {
		margin-top: 80px; }
	@include md {
		position: absolute;
		top: 0;
		right: -250px;
		margin-top: 0;
		padding: 80px 0 20px;
		width: 250px;
		transition: transform .3s ease-in-out;
		overflow-y: auto;
		background-color: $white;
		&.is-open {
			transform: translateX(-250px); } }
	&__item {
		vertical-align: top;
		display: inline-block;
		@include md {
			display: block; }
		&:not(:first-child) {
			margin-left: 39px;
			@include md {
				margin: 15px 0 0; } } }
	&__link {
		position: relative;
		line-height: 1;
		color: $white;
		padding-bottom: 15px;
		display: block;
		@include md {
			padding-left: 15px;
			padding-right: 15px;
			color: $black; }
		.is-active & {
			font-weight: 700;
			&:before {
				transform: scaleX(1); } }
		@include hover {
			&:before {
				transform: scaleX(1); } }
		&:before {
			position: absolute;
			content: "";
			left: 0;
			right: 0;
			bottom: 0;
			height: 2px;
			transition: transform .2s ease-in-out;
			transform: scaleX(0);
			border-radius: 2px;
			background-color: $radicalRed; } } }
