.breadcrumbs {
	margin-left: 156px;
	@include lg {
		margin-left: 0; }
	&__item {
		position: relative;
		vertical-align: top;
		font-size: 15px;
		display: inline-block;
		@include sm {
			font-size: 13px; }
		&:not(:first-child) {
			padding-left: 28px;
			&:before {
				position: absolute;
				content: "";
				top: 5px;
				left: 9px;
				width: 8px;
				height: 14px;
				background: url(../img/arr.svg) no-repeat 0 0;
				@include sm {
					top: 1px; } } } }
	a {
		color: $white;
		font-weight: 400;
		border-bottom: 1px solid transparent;
		transition: border-color .3s;
		@include hover {
			border-color: $white; } }
	span {
		font-weight: 300; } }
