.out {
	position: relative;
	overflow: hidden; }
.inner {
	position: relative;
	padding-left: 15px;
	padding-right: 15px;
	margin: 0 auto;
	max-width: 1238px;
	@include clr; }

.row {
	margin: 0 -16px;
	@include clr;
	@include lg {
		margin: 0 -15px; } }

$cols: 50 100;

.col {
	float: left;
	padding: 16px;
	width: 100%;
	@include sm {
		padding: 15px; }
	@each $i in $cols {
		&-#{$i} {
			width: $i + %; } }
	&_md {
		@each $i in $cols {
			&-#{$i} {
				@include md {
					width: $i + %; } } } }
	&_sm {
		@each $i in $cols {
			&-#{$i} {
				@include sm {
					width: $i + %; } } } } }
